@import url("https://fonts.googleapis.com/css2?family=Playfair+Display+SC:wght@400;700&family=Poppins:wght@300;500;700&family=Roboto:wght@300;500;700&display=swap");
@font-face {
  font-family: "Medium-font", sans-serif;
  src: url("/dist/fonts/Laila Medium.ttf");
}
html {
  scroll-behavior: smooth;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

body {
  font-family: "Medium-font", sans-serif;
  font-size: 16px;
  font-weight: 300;
}

h1,
h2,
h3,
h4,
h5,
h6,
header,
a {
  font-family: "Playfair Display SC", serif;
}

.bg-body-tertiary {
  --bs-bg-opacity: 0;
  background-color: inherit;
}

.bg-light {
  background-color: #ffffff !important;
  -webkit-box-shadow: 0px 1px 3px 1px rgba(246, 116, 209, 0.2);
          box-shadow: 0px 1px 3px 1px rgba(246, 116, 209, 0.2);
}

.btn-light {
  -webkit-box-shadow: 0px 5px 10px 2px #ffa6d7;
          box-shadow: 0px 5px 10px 2px #ffa6d7;
}

hr {
  margin: 50px auto;
  max-width: 350px;
  border-top: 2px solid #ff63e8;
}

a {
  text-decoration: none;
  color: #000000;
}

.navbar-nav {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.navbar-collapse {
  -webkit-box-flex: 0;
      -ms-flex-positive: 0;
          flex-grow: 0;
}

.nav-link:hover {
  border-bottom: 1px solid #ffa6d7;
}

.preview {
  background-color: #ffffff;
}
.preview-img {
  border-bottom: 1px solid #ffa6d7;
}

.socials {
  margin: 0;
  padding: 0;
  list-style: none;
}
.socials-links svg {
  margin: 0 auto;
  width: 40px;
  height: auto;
}
.socials-links svg:hover {
  -webkit-transform: scale(1.25);
      -ms-transform: scale(1.25);
          transform: scale(1.25);
  -webkit-transition: 0.2s ease;
  -o-transition: 0.2s ease;
  transition: 0.2s ease;
}

.swiper-wrapper {
  -webkit-transition-timing-function: linear;
       -o-transition-timing-function: linear;
          transition-timing-function: linear;
}
.swiper-slide img {
  width: 64px;
  display: block;
  -o-object-fit: cover;
     object-fit: cover;
}

.zodiac-signs-img {
  width: 64px;
}

.insta-link {
  text-transform: uppercase;
  border: 1px solid #ffa6d7;
}
.insta-link:hover {
  border: 1px solid #ff63e8;
}

.about-quality {
  padding: 30px 10px;
  background: rgba(255, 255, 255, 0);
  border-radius: 16px;
  border: 1px solid rgb(0, 255, 242);
}
.about-quality__item {
  max-width: 500px;
  margin: 0 auto;
  text-align: center;
}
.about-values {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.about-values_item {
  width: 50%;
  padding: 20px;
  margin-bottom: 15px;
  border-radius: 30px;
  color: #000000;
  background-color: #ff63e8;
  background-image: -o-linear-gradient(135deg, #ff63e8 0%, #ffa6d7 74%);
  background-image: linear-gradient(315deg, #ff63e8 0%, #ffa6d7 74%);
  -webkit-box-shadow: 5px 5px 20px rgba(203, 205, 211, 0.1);
          box-shadow: 5px 5px 20px rgba(203, 205, 211, 0.1);
  text-transform: uppercase;
  --border-size: 3px;
  --border-angle: 0turn;
  background-image: conic-gradient(from var(--border-angle), #ffffff, rgb(255, 255, 255) 50%, #ffffff), conic-gradient(from var(--border-angle), transparent 20%, rgb(0, 255, 242), rgb(255, 93, 225));
  background-size: calc(100% - var(--border-size) * 2) calc(100% - var(--border-size) * 2), cover;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-animation: bg-spin 3s linear infinite;
          animation: bg-spin 3s linear infinite;
}
@-webkit-keyframes bg-spin {
  to {
    --border-angle: 1turn;
  }
}
@keyframes bg-spin {
  to {
    --border-angle: 1turn;
  }
}
.about-values_item:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.about-values_item:last-child {
  margin-bottom: 0px;
}
.about-values_item p {
  text-align: center;
  margin-bottom: 0;
}

@property --border-angle {
  syntax: "<angle>";
  inherits: true;
  initial-value: 0turn;
}
.services {
  overflow: hidden;
}
@media (max-width: 992px) {
  .services .solar-wrapper {
    height: 450px;
  }
}
@media (max-width: 576px) {
  .services .solar-wrapper {
    height: 250px;
  }
}

.accordion {
  --bs-accordion-border-color: none;
  --bs-accordion-btn-padding-y: 1.5rem;
}
.accordion-button {
  -webkit-box-shadow: #ffa6d7 0px 3px 5px 2px;
          box-shadow: #ffa6d7 0px 3px 5px 2px;
}

.accordion-button:not(.collapsed) {
  background-color: white;
}

.footer .row > * {
  width: auto;
}
@media (max-width: 370px) {
  .footer-logo {
    display: none !important;
  }
  .footer .row {
    -webkit-box-pack: center !important;
        -ms-flex-pack: center !important;
            justify-content: center !important;
  }
}